@keyframes scale-in-top {
    0% {
      transform: scale(0);
      transform-origin: 0% 0% 50%;
      opacity: 0;
    }
    100% {
      transform: scale(1);
      transform-origin: 0% 0% 50%;
      opacity: 1;
    }
  }
  @keyframes scale-out-top {
    0% {
      transform: scale(1);
      transform-origin: 50% 0%;
      opacity: 0;
    }
    100% {
      transform: scale(0);
      transform-origin: 50% 0%;
      opacity: 1;
    }
  }